* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


.index-wrapper {
    width: 100%;
    height: 100vh;
    position: relative;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 3fr;
}

.index-sub-wrapper {
    margin: 0 auto;
    margin-top: 100px;
}

.triangle-red {
    width: 0;
    height: 0;
    border-bottom: 400px solid red;
    border-left: 200px solid transparent;
    position: absolute;
    top: -100px;
    right: 100px;
    transform: rotate(-90deg);
    z-index: 2;
}

.triangle-blue {
    width: 0;
    height: 0;
    border-bottom: 400px solid rgb(53, 154, 255);
    border-left: 200px solid transparent;
    position: absolute;
    top: 0;
    right: 0;
    transform: rotateX(180deg);
}


.triangle-red-bottom {
    width: 0;
    height: 0;
    border-top: 200px solid transparent;
    border-left: 400px solid red;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 3;
}

.triangle-blue-bottom {
    width: 0;
    height: 0;
    border-bottom: 400px solid rgb(53, 154, 255);
    border-right: 200px solid transparent;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
}

.index-daejeon {
    margin: 0 auto;
    text-align: center;
    animation: fadein 3s;
}

.index-daejeon h1:first-child {
    font-size: 60px;
    font-family: '강원교육튼튼';
    margin-bottom: 10px;
}

.index-daejeon h1:last-child {
    font-size: 30px;
    color: #2a2a2a;
    font-weight: 400;
    font-family: 'NanumSquareNeo-dEb';
}

.big-red-strong {
    color: rgb(239, 26, 26);
    margin-right: 15px;
    font-family: '강원교육튼튼';
}

.big-black-strong {
    font-weight: 900;
    margin-right: 10px;

}

.point-red {
    font-size: 45px;
    color: rgb(239, 26, 26);
    font-family: '정선';
}

.last-big-black-strong {
    font-weight: 900;
    margin-left: 10px;
}

.index-Box {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    text-align: center;
    margin-top: 80px;
    animation: fadein 3s;
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.index-BoxList {
    width: 380px;
    height: 400px;
    margin-right: 40px;
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0 7px 7px 7px rgb(228, 215, 215);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 300ms ease-out;
}

.index-BoxList:first-child:hover {
    background-color: #fff1f1;
    margin-top: -12px;
}

.index-BoxList:last-child:hover {
    background-color: #f1faff;
    margin-top: -12px;
}

.index-BoxList:last-child {
    margin-right: 0;
}

.index-Boxtem {
    padding: 50px;
}

.index-bottom-imgbox {
    width: 280px;
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.index-bottom-img1 {
    width: 170px;
}

.index-bottom-img2 {
    width: 250px;
}


.index-Boxtem h1 {
    color: rgb(172, 19, 19);
    font-size: 30px;
    font-weight: 900;
    padding: 30px 0;
    font-family: 'NanumSquareNeo-dEb';
    font-weight: 700;
}

.index-Boxtem .index-text-2 {
    color: rgb(49, 63, 196);
}

.index-Boxtem p {
    margin: 0 auto;
    color: #5e5e5e;
    font-weight: 400;
    font-family: 'Pretendard-Medium';
}

.index-footer {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 20px;
    font-family: 'Pretendard-Medium';
}

.index-footer p {
    font-size: 13px;
}

.index-footer-call {
    color: #464646;
    margin-top: 5px;
    margin-bottom: 2px;
}

.index-footer-img {
    width: 180px;
}

.index-footer-call+p {
    color: #a3a3a3;
}

.index-footer span {
    margin-right: 5px;
}


@media screen and (max-width: 1250px) {

    .triangle-red {
        border-bottom: 300px solid red;
        border-left: 150px solid transparent;
        top: -75px;
        right: 75px;
    }

    .triangle-blue {
        border-bottom: 300px solid rgb(53, 154, 255);
        border-left: 150px solid transparent;
    }


    .triangle-red-bottom {
        border-top: 150px solid transparent;
        border-left: 300px solid red;
    }

    .triangle-blue-bottom {
        border-bottom: 300px solid rgb(53, 154, 255);
        border-right: 150px solid transparent;
    }

    .index-daejeon h1:first-child {
        font-size: 55px;
        margin-bottom: 15px;
    }

    .index-daejeon h1:last-child {
        font-size: 30px;
    }

    .big-red-strong {
        margin-right: 10px;
    }

    .big-black-strong {
        margin-right: 5px;
    }

    .point-red {
        font-size: 40px;
    }

    .last-big-black-strong {
        margin-left: 7px;
    }


    .index-Box {
        width: 700px;
        margin-top: 100px;
    }

    .index-BoxList {
        width: 300px;
        height: 340px;
        margin-right: 30px;
        border-radius: 10px;
        box-shadow: 0 5px 5px 5px rgb(228, 215, 215);
    }

    .index-bottom-imgbox {
        width: 300px;
        height: 180px;
    }

    .index-bottom-img1 {
        width: 170px;
    }

    .index-bottom-img2 {
        width: 220px;
    }


    .index-Boxtem h1 {
        font-size: 25px;
        padding: 20px 0;
    }

    .index-Boxtem p {
        font-size: 16px;
    }

    .index-footer {
        margin-bottom: 80px;
        font-family: 'Pretendard-Medium';
    }

    .index-footer p {
        font-size: 14px;
    }

    .index-footer-img {
        width: 140px;
    }
}

