        margin-right: 0px;
        border-radius: 8px;
        box-shadow: 0 3px 3px 3px rgb(228, 215, 215);
    }

    .index-BoxList:first-child:hover,
    .index-BoxList:last-child:hover {
        margin-top: 0;
    }

    .index-BoxList:first-child {
        margin-bottom: 40px;
    }

    .index-bottom-imgbox {
        width: 200px;
        height: 110px;
    }


    .index-bottom-img1 {
